import styled from "styled-components";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { InnerWrap, Text, Title2 } from "../../assets/styles/component";

import icon_assigment from "../../assets/images/icon_assignment.svg";
import icon_creditcard from "../../assets/images/icon_credit-card.svg";
import icon_electricity from "../../assets/images/icon_electricity.svg";
import icon_tag from "../../assets/images/icon_tag.svg";
import icon_volunteer from "../../assets/images/icon_volunteer.svg";
import icon_crown from "../../assets/images/icon_crown.svg";
import Future_1 from "../../assets/images/Future_1.webp";
import Future_2 from "../../assets/images/Future_2.webp";
import { useEffect, useRef, useState } from "react";

const SFuture = styled.section`
  overflow: hidden;
  transform: translateY(-70px);
  
  background: radial-gradient(
      87.41% 29.55% at 49.09% 103.28%,
      #06a5d8 20.47%,
      rgba(0, 51, 233, 0.637795) 59.04%,
      rgba(41, 1, 157, 0) 100%
    ),
    radial-gradient(
      68.14% 62.26% at 42.1% -14.94%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #29009c;

  padding: 70px 0 70px;
  border-radius: 30px;

  & > div > p {
    text-align: center;
    margin: 0 auto;
  }
`;

const Title = styled(Title2)`
  margin-bottom: 30px;
  font-size: 26px;
`;

const FrameSlider = styled.div`
  position: relative;
  margin-top: 80px;
  margin-bottom: 50px;
  width: 100%;
  min-height: 70vw;
`;

const Frame = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);

  margin: 0 auto;
  overflow: hidden;
  max-width: 90vw;
  height: fit-content;
  width: 100%;
  border-radius: 14px;
  outline: 5px solid #ffffff22;

  background: radial-gradient(
    240.7% 80.8% at 107.77% 65.48%,
    #a927ca 0%,
    #d72f75 41.67%,
    #e79079 76.04%,
    #e3592f 98.44%
  );

  img {
    width: 100%;
    margin-bottom: -5px;
  }

  transition: 0.5s;
  opacity: 0;

  &.prev {
    transform: translateX(-70%) rotate(-2.5deg);
    opacity: 0.5;
    z-index: 1;
  }

  &.active {
    z-index: 2;
    opacity: 1;
    transform: translateX(-50%) rotate(0.5deg);
  }

  &.next {
    transform: translateX(-30%) translateY(7%) rotate(3.5deg);
    opacity: 0.5;
    z-index: 1;
  }
`;

const Links = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin: 0 auto;
  margin-top: 50px;
  width: 100%;
`;

const Link = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 20px;
  gap: 10px;
  width: fit-content;
  border-radius: 51px;

  p {
    font-size: 12px;
  }

  ${({ color }) => {
    switch (color) {
      case "orange":
        return `
        background: #e3592f;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        transform: rotate(2.17deg); `;

      case "pink":
        return `
        background: #D72F75;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        transform: translateX(-10%) rotate(-3.1deg); `;

      case "yellow":
        return `
        background: #EBA050;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        transform:  translateX(10%) rotate(1.84deg); `;

      case "whiteblue":
        return `
        background: #0079E6;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        transform:  translateX(-30%) rotate(-2.89deg); `;

      case "violet":
        return `
        background: #9C00E8;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        transform:  translateX(10%)  rotate(1.84deg); `;

      case "blue":
        return `
        background: #1010F2;
        box-shadow: inset 0px 0px 38px rgba(255, 255, 255, 0.29);
        transform:  translateX(-10%)  rotate(-3.91deg); `;
    }
  }}
`;

const Icon = styled(LazyLoadImage)`
  max-width: 35px;
`;

const Dots = styled.div`
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 15px;

  & > div {
    position: relative;
    width: 4px;
    height: 4px;
    border-radius: 10px;
    background-color: #ffffff60;

    svg {
      display: none;
      pointer-events: none;
    }

    &.active {
      background-color: #fff;

      svg {
        display: block;

        animation: circle 6s linear;

        @keyframes circle {
          0% {
            stroke-dashoffset: 0;
          }
          100% {
            stroke-dashoffset: 62;
          }
        }
      }
    }

    svg {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(-50%, -50%) scale(0.4);
      transform-origin: center;
      transition: storke-dashoffset 0.3s;

      .progress-ring__circle {
        stroke-dasharray: 62, 62;
      }
    }
  }
`;

const Future = ({ language }) => {
  const Slider = useRef(null);
  const dots = useRef(null);
  const [activeSlide, setActiveSlide] = useState(1);

  useEffect(() => {
    let slider = Slider.current;

    let slides = slider.querySelectorAll("div");
    slides.forEach((slide, index) => {
      slide.classList.remove("prev");
      slide.classList.remove("next");
    });

    slides.forEach((slide, index) => {
      if (slide.classList.contains("active")) {
        let prevSlide = slides[index - 1];
        let nextSlide = slides[index + 1];

        if (prevSlide) prevSlide.classList.add("prev");
        if (nextSlide) nextSlide.classList.add("next");
      }
    });

    setTimeout(() => {
      if (activeSlide == 4) {
        setActiveSlide(1);
      } else {
        setActiveSlide(activeSlide + 1);
      }
    }, 6000);
  }, [activeSlide]);

  return (
    <SFuture>
      <InnerWrap>
        <Title>
          Как будет <br /> выглядеть <br /> ваша компания
        </Title>

        <FrameSlider ref={Slider}>
          <Frame
            className={activeSlide == 1 ? "active" : ""}
            active={activeSlide == 1 ? true : false}
            onClick={() => {
              setActiveSlide(1);
            }}
          >
            <LazyLoadImage src={Future_1} alt="Future" loading="lazy" />
          </Frame>

          <Frame
            className={activeSlide == 2 ? "active" : ""}
            active={activeSlide == 2 ? true : false}
            onClick={() => {
              setActiveSlide(2);
            }}
          >
            <LazyLoadImage src={Future_2} alt="Future" loading="lazy" />
          </Frame>

          <Frame
            className={activeSlide == 3 ? "active" : ""}
            active={activeSlide == 3 ? true : false}
            onClick={() => {
              setActiveSlide(3);
            }}
          >
            <LazyLoadImage src={Future_1} alt="Future" loading="lazy" />
          </Frame>

          <Frame
            className={activeSlide == 4 ? "active" : ""}
            active={activeSlide == 4 ? true : false}
            onClick={() => {
              setActiveSlide(4);
            }}
          >
            <LazyLoadImage src={Future_2} alt="Future" loading="lazy" />
          </Frame>

          <Dots ref={dots}>
            <div
              onClick={() => {
                setActiveSlide(1);
              }}
              className={activeSlide == 1 ? "active" : ""}
            >
              <svg width="120" height="120">
                <circle
                  className="progress-ring__circle"
                  stroke="#A927CA"
                  strokeWidth="2"
                  cx="65"
                  cy="65"
                  r="10"
                  fill="transparent"
                />
              </svg>
            </div>

            <div
              onClick={() => {
                setActiveSlide(2);
              }}
              className={activeSlide == 2 ? "active" : ""}
            >
              <svg width="120" height="120">
                <circle
                  className="progress-ring__circle"
                  stroke="#A927CA"
                  strokeWidth="2"
                  cx="65"
                  cy="65"
                  r="10"
                  fill="transparent"
                />
              </svg>
            </div>

            <div
              onClick={() => {
                setActiveSlide(3);
              }}
              className={activeSlide == 3 ? "active" : ""}
            >
              <svg width="120" height="120">
                <circle
                  className="progress-ring__circle"
                  stroke="#A927CA"
                  strokeWidth="2"
                  cx="65"
                  cy="65"
                  r="10"
                  fill="transparent"
                />
              </svg>
            </div>

            <div
              onClick={() => {
                setActiveSlide(4);
              }}
              className={activeSlide == 4 ? "active" : ""}
            >
              <svg width="120" height="120">
                <circle
                  className="progress-ring__circle"
                  stroke="#A927CA"
                  strokeWidth="2"
                  cx="65"
                  cy="65"
                  r="10"
                  fill="transparent"
                />
              </svg>
            </div>
          </Dots>
        </FrameSlider>

        <Title>А что еще?</Title>

        <Text fix_size="12" size="12">
          Вместо обычных ссылок вы сможете интегрировать в неограниченное
          <br />
          количество стримов уникальный <br /> промо-формат
        </Text>

        <Links>
          <Link color={"orange"}>
            <Icon src={icon_electricity} alt="Idea" loading="lazy" />
            <Text fix_size="12" size="12">
              User-generated контент
            </Text>
          </Link>

          <Link color={"pink"}>
            <Icon src={icon_creditcard} alt="Card" loading="lazy" />
            <Text fix_size="12" size="12">
              Поддержка подписок
            </Text>
          </Link>

          <Link color={"yellow"}>
            <Icon src={icon_volunteer} alt="Loyalty" loading="lazy" />
            <Text fix_size="12" size="12">
              Лояльность аудитории
            </Text>
          </Link>

          <Link color={"whiteblue"}>
            <Icon src={icon_tag} alt="Tag" loading="lazy" />
            <Text fix_size="12" size="12">
              White label
            </Text>
          </Link>

          <Link color={"violet"}>
            <Icon src={icon_crown} alt="Crown" loading="lazy" />
            <Text fix_size="12" size="12">
              Тонкий таргетинг
            </Text>
          </Link>

          <Link color={"blue"}>
            <Icon src={icon_assigment} alt="Document" loading="lazy" />
            <Text fix_size="12" size="12">
              Full service
            </Text>
          </Link>
        </Links>
      </InnerWrap>
    </SFuture>
  );
};

export default Future;
